import { BoundarySetupModel } from '../model/boundary-set.model';

/**
 * The Moon service
 */
export class MoonService {
  /**
   * the left boundary
   */
  private leftBoundary: number;
  /**
   * the right boundary
   */
  private rightBoundary: number;
  /**
   * the boundary stake size
   */
  private boundaryStakeWidth: number;
  /**
   * the boundary stake Height
   */
  private boundaryStakeHeight: number;
  /**
   * The constructor
   *
   * @param ctx The CTX
   * @param boundarySetUp The Boundary Set up
   */
  constructor(private ctx: CanvasRenderingContext2D, private boundary: BoundarySetupModel) {
    this.leftBoundary = this.boundary.x + 40;
    this.rightBoundary = this.boundary.width - 80 + this.leftBoundary;
    this.boundaryStakeWidth = 5;
    this.boundaryStakeHeight = this.boundary.height / 2;
  }

  /**
   * Draw the moon
   */
  drawTheMoon(): void {
    this.ctx.fillStyle = 'rgba(34, 65, 4)';
    this.ctx.fillRect(this.boundary.x, this.boundary.y, this.boundary.width, this.boundary.height);

    this.ctx.fillStyle = 'rgba(8, 136, 23)'; //  Green
    this.ctx.fillRect(
      this.leftBoundary - this.boundaryStakeWidth,
      this.boundary.y,
      this.boundaryStakeWidth,
      this.boundaryStakeHeight
    );

    this.ctx.fillStyle = 'rgba(200, 95, 36)'; // Light Orange
    this.ctx.fillRect(this.rightBoundary, this.boundary.y, this.boundaryStakeWidth, this.boundaryStakeHeight);
  }

  /**
   * Animate the invasion
   */
  public animate(): void {
    this.drawTheMoon();
  }

  /**
   * Get the moonTop
   */
  public getMoonTop(): number {
    return this.boundary.y;
  }

  /**
   * Get the left boundary
   */
  public getMoonLeftBoundary(): number {
    return this.leftBoundary - 1;
  }

  /**
   * Get the right boundary
   */
  public getMoonRightBoundary(): number {
    return this.rightBoundary + 1;
  }
}
