import { ASSET_ANIMATION, COLORS } from './moon-defense-constants';
import { GameService } from '../service/game.service';
import { ObservableTypeEnum } from '../enum/observable-type.enum';
import { ObservableModel } from '../model/observable.model';
import { AssetClass } from '../asset/asset.class';
import { IAsset } from '../interface/asset.interface';
import { BoundarySetupModel } from '../model/boundary-set.model';

/**
 * The Moon Defense Class
 */
export class MoonDefenseClass extends AssetClass {
  /**
   * Constructor
   * @param gameService The GameService
   * @param ctx The ctx
   * @param boundarySetup The BoundarySetupModel
   * @param guid The guid of the alien
   */
  constructor(
    gameService: GameService,
    ctx: CanvasRenderingContext2D,
    boundarySetup: BoundarySetupModel,
    guid: number
  ) {
    super(gameService, ctx, boundarySetup, guid);
  }

  /**
   * Spawn the moonDefense
   */
  protected override spawn(): void {
    this.restore();
    super.spawn();
  }

  /**
   * Restore the spawn
   */
  public override restore(): void {
    this.assetAnimation = JSON.parse(JSON.stringify(ASSET_ANIMATION[0])) as number[][];
    this.engineAnimation = [];

    super.restore();
  }

  /**
   * Draw evenything necessary
   */
  protected draw(): void {
    this.ctx.save();
    this.ctx.translate(this.x, this.y);
    this.drawMoonDefense();
    this.ctx.restore();
  }

  /**
   * Draw the moonDefense
   */
  private drawMoonDefense(): void {
    this.assetAnimation.forEach((row, y) => {
      row.forEach((value, x) => {
        if (value > 0) {
          this.ctx.fillStyle = COLORS[value];
          this.ctx.fillRect(x, y, 1, 1);
        }
      });
    });
  }

  /**
   * Is the moon defense hit
   * @param bombAsset The Bomb Asset
   */
  isHitBomb(bombAsset: IAsset): void {
    if (!this.isDestroyed) {
      const defenseHit = this.gameService.isHit(
        {
          x: this.x,
          y: this.y,
          shape: this.shape
        } as IAsset,
        bombAsset
      );

      if (defenseHit) {
        const assetX = Math.round(bombAsset.x - this.x);

        let rowHit = -1;
        for (let y = 0; y < this.getAssetHeight(); y++) {
          if (this.assetAnimation[y][assetX] > 0) {
            rowHit = y;
            break;
          }
        }

        for (let y = rowHit; y < rowHit + bombAsset.shape.length; y++) {
          const row = this.assetAnimation[y];
          if (row) {
            const nextColumn = assetX + 1;
            this.assetAnimation[y][assetX] = 0;
            this.assetAnimation[y][nextColumn] = 0;
            this.shape[y][assetX] = 0;
            this.shape[y][nextColumn] = 0;
          } else {
            break;
          }
        }

        bombAsset.isDestroyed = true;

        let counter = 0;
        this.assetAnimation.map((row) => {
          row.map((value: number) => {
            counter += value;
          });
        });

        this.isDestroyed = counter === 0;
      }
    }
  }

  /**
   * Is the defense hit by a missile
   * @param missileAsset The Missile Asset
   */
  isHitMissile(missileAsset: IAsset): void {
    if (!this.isDestroyed) {
      const defenseHit = this.gameService.isHit(
        {
          x: this.x,
          y: this.y,
          shape: this.shape
        } as IAsset,
        missileAsset
      );

      if (defenseHit) {
        const assetX = Math.round(missileAsset.x - this.x);

        let rowHit = -1;
        for (let y = this.getAssetHeight() - 1; y >= 0; y--) {
          if (this.assetAnimation[y][assetX] > 0) {
            rowHit = y;
            break;
          }
        }

        for (let y = rowHit; y > rowHit - missileAsset.shape.length; y--) {
          const row = this.assetAnimation[y];
          if (row) {
            const nextColumn = assetX + 1;
            this.assetAnimation[y][assetX] = 0;
            this.assetAnimation[y][nextColumn] = 0;
            this.shape[y][assetX] = 0;
            this.shape[y][nextColumn] = 0;
          } else {
            break;
          }
        }

        missileAsset.isDestroyed = true;

        let counter = 0;
        this.assetAnimation.map((row) => {
          row.map((value: number) => {
            counter += value;
          });
        });

        this.isDestroyed = counter === 0;
      }
    }
  }

  /**
   * Animate the moonDefense
   */
  public animate(): void {
    if (this.isDestroyed || this.isGameOver) {
      this.gameService.emitMasterObservableEvent({
        type: ObservableTypeEnum.moonDefenseDestroyed,
        numberData: this.uid
      } as ObservableModel);
      return;
    }

    this.draw();
  }
}
