import { ASSET_ANIMATION, ENGINE_ANIMATION } from './spaceship-1-constants';
import { GameService } from '../../service/game.service';
import { SpaceshipClass } from '../spaceship/spaceship.class';
import { BoundarySetupModel } from '../../model/boundary-set.model';

/**
 * The Spaceship 1 Class
 */
export class Spaceship1Class extends SpaceshipClass {
  /**
   * Constructor
   * @param gameService The GameService
   * @param ctx The ctx
   * @param boundarySetup The BoundarySetupModel
   */
  constructor(gameService: GameService, ctx: CanvasRenderingContext2D, boundarySetup: BoundarySetupModel) {
    super(gameService, ctx, boundarySetup, true);
  }

  /**
   * Spawn the spaceship
   */
  protected override spawn(): void {
    this.assetAnimations = ASSET_ANIMATION;
    this.engineAnimations = ENGINE_ANIMATION;
    super.spawn();
  }
}
