import { Component, OnInit, Inject } from '@angular/core';
import { GameConfigurationModel } from '../model/game-configuration.model';
import { DebugAssetsModel } from '../model/debug-assets.model';
import { AssetConfigurationModel } from '../model/asset-configuration.model';
import { GameCookieService } from '../service/game-cookie.service';
import { DebugDataModel } from '../model/debug-data.model';

/**
 * The space invaders component
 */
@Component({
  selector: 'bp-space-invaders',
  templateUrl: './space-invaders.component.html',
  styleUrls: ['./space-invaders.component.scss', '../font/press-start-2p.scss']
})
export class SpaceInvadersComponent implements OnInit {
  /**
   * The debug variable
   */
  isDebug: boolean;
  /**
   * Debug Assets
   */
  debugAssets: DebugAssetsModel;
  /**
   * Debug Assets
   */
  assets: AssetConfigurationModel[];

  /**
   * Constructor
   *
   * @param gameConfiguration The Game Configuration
   * @param gameCookie The Game Cookie Service
   */
  constructor(
    @Inject('GameConfigurationData') gameConfiguration: GameConfigurationModel,
    private gameCookieService: GameCookieService
  ) {
    this.isDebug = gameConfiguration.isDebug;

    this.debugAssets = {
      spaceship: true,
      alienBoss: false,
      alien: false,
      missile: false,
      number: false,
      bomb: false,
      defense: false
    } as DebugAssetsModel;

    this.assets = [
      {
        key: 'spaceship',
        name: 'Spaceship'
      } as AssetConfigurationModel,
      {
        key: 'alien',
        name: 'Alien'
      } as AssetConfigurationModel,
      {
        key: 'alienBoss',
        name: 'Alien Boss'
      } as AssetConfigurationModel,
      {
        key: 'bomb',
        name: 'Bomb'
      } as AssetConfigurationModel,
      {
        key: 'missile',
        name: 'Missile'
      } as AssetConfigurationModel,
      {
        key: 'defense',
        name: 'Defense'
      } as AssetConfigurationModel,
      {
        key: 'number',
        name: 'Number'
      } as AssetConfigurationModel
    ];
  }

  /**
   * On Init
   */
  ngOnInit() {
    this.toggleAssets(this.gameCookieService.getDebugData());
  }

  /**
   * toggleAssets
   *
   * @param asset The asset to display
   */
  toggleAssets(asset: string): void {
    Object.keys(this.debugAssets).map((key: string) => {
      if (key === asset) {
        /* eslint-disable-next-line */
        // @ts-ignore
        this.debugAssets[key] = true;
        this.gameCookieService.setDebugData({
          assetName: key
        } as DebugDataModel);
      } else {
        /* eslint-disable-next-line */
        // @ts-ignore
        this.debugAssets[key] = false;
      }
    });
  }
}
