import { MoonDefenseClass } from '../moon-defense/moon-defense.class';
import { BoundarySetupModel } from '../model/boundary-set.model';
import { GameService } from './game.service';
import { AssetAlignEnum } from '../asset/enum/asset-align.enum';
import { AssetAlignVerticalEnum } from '../asset/enum/asset-align-vertical.enum';
import { IAsset } from '../interface/asset.interface';
import { ObservableModel } from '../model/observable.model';
import { ObservableTypeEnum } from '../enum/observable-type.enum';

/**
 * The Moon Defense service
 */
export class MoonDefenseService {
  /**
   * The moon defense
   */
  private moonDefenses: MoonDefenseClass[];

  /**
   * The constructor
   *
   * @param gameService The GameService
   * @param ctx The CTX
   * @param boundarySetUp The Boundary Set up
   */
  constructor(
    private gameService: GameService,
    private ctx: CanvasRenderingContext2D,
    private boundary: BoundarySetupModel
  ) {
    this.moonDefenses = [];

    this.setUp();

    this.gameService.getMasterObservable().subscribe((resultModel: ObservableModel) => {
      if (resultModel.type === ObservableTypeEnum.moonDefenseDestroyed) {
        this.moonDefenses[resultModel.numberData].isDestroyed = true;
      } else if (resultModel.type === ObservableTypeEnum.alienDrop) {
        this.aliensDestroyedMoonbase(resultModel.numberData);
      }
    });
  }

  /**
   * Determine if the aliens have destroyed the moonbases
   * @param alienYPosition The bottom Alien Y Position
   */
  private aliensDestroyedMoonbase(alienYPosition: number): void {
    const moonbase = this.getActiveMoonBases().pop();
    if (moonbase && alienYPosition > moonbase.y) {
      this.getActiveMoonBases().map((moonDefense: MoonDefenseClass) => {
        moonDefense.isDestroyed = true;
      });
    }
  }

  /**
   * Set up the bases
   */
  private setUp(): void {
    Array(3)
      .fill(0)
      .map(() => {
        this.moonDefenses.push(
          new MoonDefenseClass(this.gameService, this.ctx, this.boundary, this.moonDefenses.length)
        );
      });
  }

  /**
   * Set up the bases
   */
  public setupMoonBase(isReset: boolean, spaceshipYPosition: number): void {
    this.moonDefenses.map((moonDefense: MoonDefenseClass, index: number) => {
      moonDefense.isDestroyed = false;
      if (isReset) {
        moonDefense.restore();
      }

      let moonAsset = moonDefense.positionAsset(AssetAlignEnum.leftFourth, AssetAlignVerticalEnum.middle);
      if (index === 1) {
        moonAsset = moonDefense.positionAsset(AssetAlignEnum.center, AssetAlignVerticalEnum.middle);
      } else if (index === 2) {
        moonAsset = moonDefense.positionAsset(AssetAlignEnum.rightFourth, AssetAlignVerticalEnum.middle);
      }

      moonDefense.move({
        x: moonAsset.x,
        y: spaceshipYPosition - moonDefense.getAssetHeight()
      } as IAsset);
    });
  }

  /**
   * Get the Active Moon Bases
   */
  public getActiveMoonBases(): MoonDefenseClass[] {
    return this.moonDefenses.filter((moonDefense: MoonDefenseClass) => !moonDefense.isDestroyed);
  }

  /**
   * Animate the invasion
   */
  public animate(): void {
    this.getActiveMoonBases().map((moonDefense: MoonDefenseClass) => {
      moonDefense.animate();
    });
  }
}
