import { Component, ViewChild, ElementRef, OnInit, HostListener } from '@angular/core';
import { COLS, BLOCK_SIZE, ROWS, LEVELSPEED, LEVELHEIGHT, MOON_HEIGHT } from './gameboard-constants';
import { GameService } from '../service/game.service';
import { MissileClass } from '../missile/missile/missile.class';
import { ObservableModel } from '../model/observable.model';
import { ObservableTypeEnum } from '../enum/observable-type.enum';
import { BombClass } from '../bomb/bomb.class';
import { MoonDefenseClass } from '../moon-defense/moon-defense.class';
import { BoundarySetupModel } from '../model/boundary-set.model';
import { TextService } from '../service/text.service';
import { GameCookieService } from '../service/game-cookie.service';
import { GameDataModel } from '../model/game-data.model';
import { InvasionService } from '../service/invasion.service';
import { AlienClass } from '../aliens/alien/alien.class';
import { MoonService } from '../service/moon.service';
import { MoonDefenseService } from '../service/moon-defense.service';
import { SpaceshipService } from '../service/spaceship.service';
import { ProjectileService } from '../service/projectile.service';

/**
 * The Gameboard Component
 */
@Component({
  selector: 'bp-gameboard',
  templateUrl: './gameboard.component.html',
  styleUrls: ['./gameboard.component.scss', '../font/press-start-2p.scss']
})
export class GameboardComponent implements OnInit {
  /**
   * Display Grid
   */
  private isDisplayGrid = false;
  /**
   * The canvas
   */
  @ViewChild('board', { static: true })
  canvas: ElementRef<HTMLCanvasElement>;
  /**
   * Canvas Rendering Context 2D
   */
  private ctx: CanvasRenderingContext2D;
  /**
   * The animation request id
   */
  private requestId: number;
  /**
   * The points
   */
  private points: number;
  /**
   * The highscore
   */
  private highScore: number;
  /**
   * The Level Display
   */
  levelDisplay: string;
  /**
   * The Game Level
   */
  gameLevel: number;
  /**
   * If the game is ovewr
   */
  private isGameOver: boolean;
  /**
   * The creep speed level
   */
  private creepSpeedLevel: number;
  /**
   * The creep speed
   */
  private creepSpeed: number;
  /**
   * The creep level
   */
  private creepLevel: number;
  /**
   * Paused
   */
  private isGamePaused: boolean;
  /**
   * Game Started;
   */
  public gameStarted: boolean;
  /**
   * lives
   */
  private lives: number;
  /**
   * text class
   */
  private textService: TextService;
  /**
   * Invasion Service
   */
  private invasionService: InvasionService;
  /**
   * Moon Service
   */
  private moonService: MoonService;
  /**
   * Moon Defense Service
   */
  private moonDefenseService: MoonDefenseService;
  /**
   * Spaceship Service
   */
  private spaceshipService: SpaceshipService;
  /**
   * Projectile Service
   */
  private projectileService: ProjectileService;

  /**
   * The Constructor
   *
   * @param gameCookieService The Game Cookie Service from DI
   * @param gameService The Game Service from DI
   */
  constructor(private gameCookieService: GameCookieService, private gameService: GameService) {
    const gameData = this.gameCookieService.getGameData();
    this.highScore = gameData.highScore;
  }

  /**
   * On Init
   */
  ngOnInit() {
    this.gameStarted = false;
    this.ctx = this.gameService.initBoard(this.canvas, COLS, ROWS, BLOCK_SIZE);

    this.moonService = new MoonService(this.ctx, {
      x: 0,
      y: ROWS - MOON_HEIGHT * 2,
      height: MOON_HEIGHT,
      width: COLS
    } as BoundarySetupModel);

    this.spaceshipService = new SpaceshipService(this.gameService, this.ctx, {
      x: this.moonService.getMoonLeftBoundary(),
      y: 0,
      height: this.moonService.getMoonTop(),
      width: this.moonService.getMoonRightBoundary()
    } as BoundarySetupModel);

    const boundary = {
      x: 0,
      y: 0,
      height: this.moonService.getMoonTop(),
      width: COLS
    } as BoundarySetupModel;

    this.moonDefenseService = new MoonDefenseService(this.gameService, this.ctx, boundary);

    this.projectileService = new ProjectileService(this.gameService, this.ctx, boundary);

    this.textService = new TextService(this.gameService, this.ctx, {
      x: 0,
      y: 0,
      height: LEVELHEIGHT[0],
      width: COLS
    } as BoundarySetupModel);

    this.invasionService = new InvasionService(this.gameService, this.ctx, {
      x: 0,
      y: 0,
      height: this.moonService.getMoonTop(),
      width: COLS
    } as BoundarySetupModel);

    this.gameService.getMasterObservable().subscribe((resultModel: ObservableModel) => {
      if (resultModel.type === ObservableTypeEnum.alienBossDestroyed) {
        this.calculateScore(resultModel.pointData);
      } else if (resultModel.type === ObservableTypeEnum.alienDestroyed) {
        this.calculateScore(resultModel.pointData);
        if (this.invasionService.aliensDestroyed()) {
          setTimeout(() => {
            this.nextLevel();
          }, 1000);
        }
      } else if (resultModel.type === ObservableTypeEnum.spaceshipDestroyed) {
        this.gameOver(false);
      }
    });

    this.resetGame();
    this.startAllAnimation();
  }

  /**
   * Evaluate the score
   *
   * @param score The current score
   */
  private calculateScore(score: number): void {
    this.points += score;
    if (this.points > this.highScore) {
      this.highScore = this.points;
      this.textService.setHighScore(this.highScore);
    }
    this.textService.setScore(this.points);
  }

  /**
   * Set the initial game board
   *
   * @param isReset if the game is reseting
   */
  private setGameBoard(isReset: boolean): void {
    /* eslint-disable-next-line */
    // @ts-ignore
    this.invasionService.setupInvasion(LEVELHEIGHT[this.creepLevel] as number);
    this.spaceshipService.setupSpaceships();
    this.moonDefenseService.setupMoonBase(isReset, this.spaceshipService.getSpaceshipYPosition());
  }

  /**
   * Pad the level
   *
   */
  /*
  private padLevel(): string {
    if (this.gameLevel < 10) {
      return `L0${this.gameLevel}`;
    }

    return `L${this.gameLevel.toString()}`;
  }
  */

  /**
   * The aliens have hit the bottom
   */
  private splat(): void {
    if (this.invasionService.splat()) {
      this.gameOver(true);
    }
  }

  /**
   * Evaluate any damage
   */
  private evaluateDamage(): void {
    this.projectileService.getActiveBombs().map((bomb: BombClass) => {
      this.spaceshipService.isHit(bomb);
      this.moonDefenseService.getActiveMoonBases().map((moonDefense: MoonDefenseClass) => {
        moonDefense.isHitBomb(bomb);
      });
    });

    this.projectileService.getActiveMissiles().map((missile: MissileClass) => {
      this.invasionService.getAllActiveAliens().map((alien: AlienClass) => {
        alien.isHit(missile);
      });

      this.moonDefenseService.getActiveMoonBases().map((moonDefense: MoonDefenseClass) => {
        moonDefense.isHitMissile(missile);
      });
    });

    if (this.invasionService.increaseInvasionSpeed(this.creepSpeed)) {
      /* eslint-disable-next-line */
      // @ts-ignore
      this.creepSpeedLevel = LEVELSPEED[this.creepSpeed++] as number;
    }
  }

  /**
   * Start All the animation in one place
   */
  private startAllAnimation(): void {
    this.draw();

    this.invasionService.animate(0, 1);

    this.moonDefenseService.animate();

    this.spaceshipService.animate();

    this.textService.animate();
  }

  /**
   * Animate
   *
   * @param now The current animation time
   */
  private animate(now = 0): void {
    this.evaluateDamage();

    this.splat();

    if (this.isGameOver || this.isGamePaused) {
      return;
    }

    this.draw();

    this.invasionService.animate(now, this.creepSpeedLevel);

    this.moonDefenseService.animate();

    this.projectileService.animate(now);

    this.spaceshipService.animate(now);

    this.textService.animate();

    this.requestId = requestAnimationFrame(this.animate.bind(this) as FrameRequestCallback);
  }

  /**
   * life lost
   */
  private lifeLost(): void {
    this.pauseGame('READY ?', 17);

    setTimeout(() => {
      /**
       * TODO
       * this.isBossing = false;
       * Associated with the spaceshipDestroyed
       */
      this.removeWeapons();

      this.pauseGame('', 0);
    }, 2000);
  }

  /**
   * Determine if the game is over
   *
   * @param isReset If this is a reset
   */
  gameOver(isReset = false): void {
    this.lives += isReset ? 3 : 1;

    if (this.points >= this.highScore) {
      this.gameCookieService.setGameData({
        highScore: this.highScore
      } as GameDataModel);
    }

    if (this.lives < 3) {
      this.lifeLost();
    } else {
      this.removeWeapons();

      setTimeout(() => {
        this.isGameOver = true;
        this.gameStarted = false;
        this.gameService.emitMasterObservableEvent({
          type: ObservableTypeEnum.gameStarted,
          booleanData: false
        } as ObservableModel);
        cancelAnimationFrame(this.requestId);
        this.gameService.emitMasterObservableEvent({
          type: ObservableTypeEnum.gameOver,
          booleanData: true
        } as ObservableModel);
        this.textService.displayText('GAME OVER', 'red', 5);
      }, 250);
    }
  }

  /**
   * Draw the board
   */
  draw() {
    this.ctx.clearRect(0, 0, this.ctx.canvas.width, this.ctx.canvas.height);
    if (this.isDisplayGrid) {
      this.gameService.addOutlines(this.ctx, 20, 0.2);
    }
    this.moonService.animate();
  }

  /**
   * Abstract event handlers
   * @param type The input type
   * @param code The key code
   */
  private handleEventListeners(type: ObservableTypeEnum, code: string): void {
    if (this.gameStarted && !this.isGamePaused) {
      this.gameService.emitMasterObservableEvent({
        type,
        stringData: code
      } as ObservableModel);
    }
  }

  /**
   * The keyEvent down listener
   * @param event the Keyboard event
   */
  @HostListener('window:keydown', ['$event'])
  keyDownEvent(event: KeyboardEvent) {
    event.preventDefault();
    this.handleEventListeners(ObservableTypeEnum.keyDownEvent, event.code);
  }

  /**
   * The keyEvent uplistener
   *
   * @param event the Keyboard event
   */
  @HostListener('window:keyup', ['$event'])
  keyUpEvent(event: KeyboardEvent) {
    event.preventDefault();
    this.handleEventListeners(ObservableTypeEnum.keyUpEvent, event.code);
  }

  /**
   * Play the game
   */
  play() {
    this.gameStarted = true;
    this.gameService.emitMasterObservableEvent({
      type: ObservableTypeEnum.gameStarted,
      booleanData: true
    } as ObservableModel);

    this.gameService.emitMasterObservableEvent({
      type: ObservableTypeEnum.gameOver,
      booleanData: false
    } as ObservableModel);

    this.resetGame();

    // If we have an old game running a game then cancel the old
    if (this.requestId) {
      cancelAnimationFrame(this.requestId);
    }

    this.animate();
  }

  /**
   * New Level Set-up
   *
   * @param isReset If the game is reseting
   */
  private newLevelSetup(isReset = false): void {
    if (this.creepLevel < 8) {
      this.creepLevel++;
    }

    this.creepSpeed = 0;
    /* eslint-disable-next-line */
    // @ts-ignore
    this.creepSpeedLevel = LEVELSPEED[this.creepSpeed++] as number;

    // this.levelDisplay = this.padLevel();

    this.invasionService.resetAlienCreep();

    this.setGameBoard(isReset);

    if (this.creepLevel > 4) {
      this.gameService.emitMasterObservableEvent({
        type: ObservableTypeEnum.alienDrop,
        numberData: ROWS * BLOCK_SIZE
      } as ObservableModel);
    }
  }

  /**
   * Remove all the weapons
   */
  private removeWeapons(): void {
    this.projectileService.removeProjectiles();
  }

  /**
   * next level
   */
  private nextLevel(): void {
    this.gameLevel++;
    this.pauseGame('LEVEL UP', 12);

    setTimeout(() => {
      this.removeWeapons();

      this.newLevelSetup();
      this.pauseGame('', 0);
    }, 2000);
  }

  /**
   * Reset the Game
   */
  resetGame() {
    this.lives = 0;
    this.creepLevel = -1;
    this.points = 0;
    this.gameLevel = 1;
    this.textService.setScore(this.points);
    this.textService.setHighScore(this.highScore);
    this.isGameOver = false;
    this.isGamePaused = false;
    this.spaceshipService.setupSpaceships();

    this.newLevelSetup(true);
  }

  /**
   * Pause the game
   *
   * @param text  The text to display
   * @param index The text offset
   */
  private pauseGame(text: string, textOffset: number): void {
    if (this.gameStarted) {
      this.isGamePaused = !this.isGamePaused;
      if (this.isGamePaused) {
        cancelAnimationFrame(this.requestId);
        this.textService.displayText(text, 'white', textOffset);
        this.gameService.emitMasterObservableEvent({
          type: ObservableTypeEnum.gamePaused,
          booleanData: true
        } as ObservableModel);
      } else {
        this.gameService.emitMasterObservableEvent({
          type: ObservableTypeEnum.gamePaused,
          booleanData: false
        } as ObservableModel);
        this.animate();
      }
    }
  }

  /**
   * Pause the game
   */
  pause() {
    this.pauseGame('PAUSED', 20);
  }
}
