import { COLORS, KEY } from './missile-constants';
import { IAsset } from '../../interface/asset.interface';
import { GameService } from '../../service/game.service';
import { AssetClass } from '../../asset/asset.class';
import { BoundarySetupModel } from '../../model/boundary-set.model';

/**
 * The Missile Class
 */
export abstract class MissileClass extends AssetClass {
  /**
   * The moves
   */
  private moves = {
    [KEY.UP]: (asset: IAsset): IAsset => ({ ...asset, y: asset.y - 5 })
  };

  /**
   * Constructor
   * @param gameService The GameService
   * @param ctx The ctx
   * @param boundarySetup The BoundarySetupModel
   * @param guid The guid of the misssle
   */
  constructor(
    gameService: GameService,
    ctx: CanvasRenderingContext2D,
    boundarySetup: BoundarySetupModel,
    guid: number
  ) {
    super(gameService, ctx, boundarySetup, guid);
  }

  /**
   * Spawn the missile
   */
  protected override spawn(): void {
    this.assetAnimation = this.assetAnimations[0];
    this.engineAnimation = this.engineAnimations[0];

    super.spawn();
  }

  /**
   * Draw evenything necessary
   */
  protected draw(): void {
    this.ctx.save();
    this.ctx.translate(this.x, this.y);
    this.drawMissile();
    this.drawEngine();
    this.ctx.restore();
  }

  /**
   * Draw the missile
   */
  private drawMissile() {
    this.assetAnimation.forEach((row, y) => {
      row.forEach((value, x) => {
        this.ctx.fillStyle = COLORS[value];
        this.ctx.fillRect(x, y, 1, 1);
      });
    });
  }

  /**
   * Draw the engine
   */
  private drawEngine() {
    this.engineAnimation.forEach((row, y) => {
      row.forEach((value, x) => {
        this.ctx.fillStyle = COLORS[value];
        this.ctx.fillRect(this.x + this.engineXOffset + x, this.y + this.engineYOffset + y, 1, 1);
      });
    });
  }

  /**
   * Animate the missile
   * @param now the current time
   */
  public animate(now = 0): void {
    this.engineTimer.elapsed = now - this.engineTimer.start;

    if (this.engineTimer.elapsed > 30) {
      this.engineTimer.start = now;
      this.engineAnimation = this.engineAnimations[this.engineAnimationCounter++ % this.engineAnimations.length];
      const asset = this.moves['ArrowUp'](this);
      if (this.gameService.valid(asset, this.boundary)) {
        this.move(asset);
      } else {
        this.isDestroyed = true;
      }
    }

    this.draw();
  }
}
