import { Component, OnInit, ElementRef, ViewChild, HostListener } from '@angular/core';

import { Spaceship1Class } from '../../spaceship-1/spaceship-1.class';
import { GameService } from '../../../service/game.service';
import { ObservableModel } from '../../../model/observable.model';
import { ObservableTypeEnum } from '../../../enum/observable-type.enum';
import { BoundarySetupModel } from '../../../model/boundary-set.model';
import { AssetConfigurationModel } from '../../../model/asset-configuration.model';
import { SpaceshipClass } from '../spaceship.class';
import { Spaceship2Class } from '../../spaceship-2/spaceship-2.class';
import { GameCookieService } from '../../../service/game-cookie.service';
import { DebugDataModel } from '../../../model/debug-data.model';

/**
 * The Spaceship Component Two
 */
@Component({
  selector: 'bp-spaceship',
  templateUrl: './spaceship.component.html',
  styleUrls: ['./spaceship.component.scss']
})
export class SpaceshipComponent implements OnInit {
  /**
   * The canvas
   */
  @ViewChild('board', { static: true })
  canvas: ElementRef<HTMLCanvasElement>;

  /**
   * CTS
   */
  private ctx: CanvasRenderingContext2D;

  /**
   * requestId
   */
  private requestId: number;

  /**
   * Spaceships
   */
  private spaceships: SpaceshipClass[];

  /**
   * request
   */
  private isAnimateOnce: boolean;

  /**
   * The spaceship
   */
  private spaceship: SpaceshipClass;

  /**
   * Debug Assets
   */
  assets: AssetConfigurationModel[];

  /**
   * The Constructor
   * @param gameService The GameService from DI
   * @param gameCookie The Game Cookie Service
   */
  constructor(private gameService: GameService, private gameCookieService: GameCookieService) {
    this.isAnimateOnce = false;
    this.assets = [
      {
        key: 'spaceship1',
        name: 'S-1'
      } as AssetConfigurationModel,
      {
        key: 'spaceship2',
        name: 'S-2'
      } as AssetConfigurationModel
    ];
  }

  /**
   * On Init
   */
  ngOnInit() {
    const rows = 20;
    const columns = 20;
    this.ctx = this.gameService.initBoard(this.canvas, columns, rows, 25);
    this.addSpaceships({
      x: 0,
      y: 0,
      height: rows,
      width: columns
    } as BoundarySetupModel);
    this.isAnimateOnce = true;
    this.toggleAssets(this.gameCookieService.getSecondaryDebugData());
    this.animate();
  }

  /**
   * Add the spaceships
   */
  private addSpaceships(boundary: BoundarySetupModel): void {
    this.spaceships = [];

    const spaceship1 = new Spaceship1Class(this.gameService, this.ctx, boundary);
    spaceship1.isDestroyed = true;
    this.spaceships.push(spaceship1);

    const spaceship2 = new Spaceship2Class(this.gameService, this.ctx, boundary, true);
    spaceship2.isDestroyed = true;
    this.spaceships.push(spaceship2);

    this.spaceship = this.spaceships[0];
    this.spaceship.isAnimateOnce = true;
    this.spaceship.isDestroyed = false;
  }

  /**
   * Animate the object once
   */
  animateAsset(): void {
    this.isAnimateOnce = !this.isAnimateOnce;
    this.spaceship.isDestroyed = false;
    cancelAnimationFrame(this.requestId);
    this.animate();
  }

  /**
   * Draw the board
   */
  private draw() {
    this.ctx.clearRect(0, 0, this.ctx.canvas.width, this.ctx.canvas.height);
    this.gameService.addOutlines(this.ctx, 1, 0.1);
  }

  /**
   * Animate
   * @param now the current time
   */
  public animate(now = 0): void {
    this.draw();
    this.spaceship.animate(now);
    if (!this.isAnimateOnce) {
      this.requestId = requestAnimationFrame(this.animate.bind(this) as FrameRequestCallback);
    }
  }

  /**
   * The keyEvent down listener
   * @param event the Keyboard event
   */
  @HostListener('window:keydown', ['$event'])
  keyDownEvent(event: KeyboardEvent) {
    event.preventDefault();
    this.gameService.emitMasterObservableEvent({
      type: ObservableTypeEnum.keyDownEvent,
      stringData: event.code
    } as ObservableModel);
  }

  /**
   * The keyEvent uplistener
   * @param event the Keyboard event
   */
  @HostListener('window:keyup', ['$event'])
  keyUpEvent(event: KeyboardEvent) {
    event.preventDefault();
    this.gameService.emitMasterObservableEvent({
      type: ObservableTypeEnum.keyUpEvent,
      stringData: event.code
    } as ObservableModel);
  }

  /**
   * toggleAssets
   *
   * @param key The asset to display
   */
  toggleAssets(key: string): void {
    this.spaceship.isDestroyed = true;
    this.draw();
    let index = 0;
    this.spaceships.map((spaceship: SpaceshipClass) => {
      spaceship.isDestroyed = true;
    });
    this.assets.map((asset: AssetConfigurationModel, $index: number) => {
      if (key === asset.key) {
        index = $index;
        this.gameCookieService.setDebugData({
          secondaryAssetName: key
        } as DebugDataModel);
      }
    });

    this.spaceship = this.spaceships[index];
    this.animateAsset();
  }
}
