import { IAsset } from '../interface/asset.interface';
import { GameService } from '../service/game.service';
import { AssetAlignEnum } from './enum/asset-align.enum';
import { AssetAlignVerticalEnum } from './enum/asset-align-vertical.enum';
import { ObservableModel } from '../model/observable.model';
import { ObservableTypeEnum } from '../enum/observable-type.enum';
import { TimerModel } from '../model/timer.model';
import { BoundaryModel } from '../model/boundary.model';
import { BoundarySetupModel } from '../model/boundary-set.model';

/**
 * The Asset Class
 */
export abstract class AssetClass implements IAsset {
  /**
   * The shape for determining the boundaries
   */
  public shape: number[][];
  /**
   * The x coordinate
   */
  public x: number;
  /**
   * The y coordinate
   */
  public y: number;
  /**
   * The animate once
   */
  public isAnimateOnce: boolean;
  /**
   * The asset to animate
   */
  protected assetAnimation: number[][];
  /**
   * The destruction to animate
   */
  protected destructionAnimation: number[][];
  /**
   * The engine animation
   */
  protected engineAnimation: number[][];
  /**
   * The request id from the animation
   */
  protected requestId: number;
  /**
   * The destroy request id from the animation
   */
  protected destructionRequestId: number;
  /**
   * The Asset Timmer
   */
  protected assetTimer: TimerModel;
  /**
   * The Destruction Timmer
   */
  protected destructionTimer: TimerModel;
  /**
   * engine Timer
   */
  protected engineTimer: TimerModel;
  /**
   * The engine X offset
   */
  protected engineXOffset: number;
  /**
   * The engine Y offset
   */
  protected engineYOffset: number;
  /**
   * Animation Counter
   */
  protected animationCounter: number;
  /**
   * Destruction Counter
   */
  protected destructionAnimationCounter: number;
  /**
   * Engine Animation Counter
   */
  protected engineAnimationCounter: number;
  /**
   * the boardary model
   */
  protected boundary: BoundaryModel;
  /**
   * Is the asset is destroyed
   */
  public isDestroyed: boolean;
  /**
   * If the asset knowns the game is over
   */
  protected isGameOver: boolean;
  /**
   * If the asset knowns the game is paused
   */
  protected isGamePaused: boolean;
  /**
   * Asset Animations
   */
  protected assetAnimations: number[][][];
  /**
   * Engine Animations
   */
  protected engineAnimations: number[][][];
  /**
   * Destruction Animations
   */
  protected destructionAnimations: number[][][];

  /**
   * Constructor
   * @param ctx The ctx
   * @param x initial x parameter
   * @param y initial y parameter
   */
  constructor(
    protected gameService: GameService,
    protected ctx: CanvasRenderingContext2D,
    protected boundarySetup: BoundarySetupModel,
    protected uid: number
  ) {
    this.x = 0;
    this.y = 0;
    this.isAnimateOnce = false;

    this.isDestroyed = false;
    this.isGameOver = false;
    this.isGamePaused = false;

    this.spawn();

    this.boundary = this.gameService.getBoundary(boundarySetup);

    this.init();
  }

  /**
   * Init the asset
   */
  init(): void {
    this.gameService.getMasterObservable().subscribe((result: ObservableModel) => {
      if (result.type === ObservableTypeEnum.keyDownEvent || result.type === ObservableTypeEnum.keyUpEvent) {
        this.processKeyStroke(result);
      } else if (result.type === ObservableTypeEnum.gameOver) {
        this.isGameOver = result.booleanData;
      } else if (result.type === ObservableTypeEnum.gamePaused) {
        this.isGamePaused = result.booleanData;
      }
    });
  }

  /**
   * Get the Asset height
   */
  getAssetHeight(): number {
    return this.shape.length;
  }

  /**
   * Get the Asset Width
   */
  getAssetWidth(): number {
    return this.shape[0].length;
  }

  /**
   * Position the Asset
   */
  positionAsset(alignPosition: AssetAlignEnum, justifyPosition: AssetAlignVerticalEnum): IAsset {
    let x = 0;
    let y = 0;

    if (alignPosition === AssetAlignEnum.center) {
      x = (this.boundary.right - this.getAssetWidth()) / 2;
    } else if (alignPosition === AssetAlignEnum.leftThird) {
      x = this.boundary.right / 3 - this.getAssetWidth() / 2;
    } else if (alignPosition === AssetAlignEnum.leftFourth) {
      x = this.boundary.right / 4 - this.getAssetWidth() / 2;
    } else if (alignPosition === AssetAlignEnum.rightThird) {
      x = (this.boundary.right * 2) / 3 - this.getAssetWidth() / 2;
    } else if (alignPosition === AssetAlignEnum.rightFourth) {
      x = (this.boundary.right * 3) / 4 - this.getAssetWidth() / 2;
    } else if (alignPosition === AssetAlignEnum.right) {
      x = this.boundary.right - this.getAssetWidth();
    }

    if (justifyPosition === AssetAlignVerticalEnum.bottom) {
      y = this.boundary.floor - this.getAssetHeight();
    } else if (justifyPosition === AssetAlignVerticalEnum.middle) {
      y = (this.boundary.floor - this.getAssetHeight()) / 2;
    }

    return {
      x: Math.round(x),
      y: Math.round(y)
    } as IAsset;
  }

  /**
   * Process the key stroke
   * @param eventCode the key event code
   */
  /* eslint-disable-next-line */
  protected processKeyStroke(event: ObservableModel): void {}

  /**
   * Spawn the asset
   */
  protected spawn(): void {
    if (!(this.assetAnimation && this.assetAnimation.length > 0)) {
      throw new Error('You must have an asset array.');
    }

    if (!this.engineAnimation) {
      throw new Error('You must have an engine array.');
    }

    this.engineXOffset = 0;
    this.engineYOffset = this.assetAnimation.length;
    this.assetTimer = { start: 0, elapsed: 0 };
    this.destructionTimer = { start: 0, elapsed: 0 };
    this.engineTimer = { start: 0, elapsed: 0 };
    this.animationCounter = 0;
    this.engineAnimationCounter = 0;
    this.destructionAnimationCounter = 0;

    this.assetTimer.start = performance.now();
    this.destructionTimer.start = performance.now();
    this.engineTimer.start = performance.now();

    this.restore();
  }

  /**
   * Restore the
   */
  restore(): void {
    /* eslint-disable-next-line */
    this.shape = JSON.parse(
      /* eslint-disable-next-line */
      JSON.stringify(this.assetAnimation.concat(JSON.parse(JSON.stringify(this.engineAnimation))))
    );
  }

  /**
   * Move the asset
   * @param asset The asset input
   */
  move(asset: IAsset): void {
    this.x = Math.round(asset.x);
    this.y = Math.round(asset.y);
  }

  /**
   * Determine if we should stop the animation
   */
  protected stopAnimation(): boolean {
    if (this.isDestroyed || this.isGameOver || this.isGamePaused || this.isAnimateOnce) {
      cancelAnimationFrame(this.requestId);
      return true;
    }

    return false;
  }
}
