import { BoundarySetupModel } from '../model/boundary-set.model';
import { GameService } from './game.service';
import { AssetAlignEnum } from '../asset/enum/asset-align.enum';
import { AssetAlignVerticalEnum } from '../asset/enum/asset-align-vertical.enum';
import { IAsset } from '../interface/asset.interface';
import { ObservableModel } from '../model/observable.model';
import { ObservableTypeEnum } from '../enum/observable-type.enum';
import { SpaceshipClass } from '../spaceships/spaceship/spaceship.class';
import { Spaceship2Class } from '../spaceships/spaceship-2/spaceship-2.class';

/**
 * The Spaceship service
 */
export class SpaceshipService {
  /**
   * The Space ship
   */
  private spaceship: SpaceshipClass;
  /**
   * Extra Space ships
   */
  private spaceships: SpaceshipClass[];
  /**
   * The gameboard bottom
   */
  private gameboardBottom: number;

  /**
   * The constructor
   *
   * @param gameService The GameService
   * @param ctx The CTX
   * @param boundarySetUp The Boundary Set up
   */
  constructor(
    private gameService: GameService,
    private ctx: CanvasRenderingContext2D,
    private boundary: BoundarySetupModel
  ) {
    this.spaceships = [];
    this.gameboardBottom = this.gameService.getGameboard().height;

    this.setUp();

    this.gameService.getMasterObservable().subscribe((resultModel: ObservableModel) => {
      if (resultModel.type === ObservableTypeEnum.spaceshipDestroyed) {
        this.spaceship.isDestroyed = false;
        this.removeSpaceshipReserve();
      }
    });
  }

  /**
   * Set up the bases
   */
  private setUp(): void {
    Array(2)
      .fill(0)
      .map(() => {
        this.spaceships.unshift(new Spaceship2Class(this.gameService, this.ctx, this.boundary, false));
      });

    let extraLivesX = 10;

    this.spaceships
      .filter((spaceship: SpaceshipClass) => !spaceship.isDestroyed)
      .map((spaceship: SpaceshipClass) => {
        spaceship.move({
          x: extraLivesX,
          y: this.gameboardBottom - spaceship.getAssetHeight()
        } as IAsset);
        extraLivesX += 20;
      });

    this.spaceship = new Spaceship2Class(this.gameService, this.ctx, this.boundary, true);
  }

  /**
   * Set up the spaceships
   */
  public setupSpaceships(): void {
    this.spaceship.isDestroyed = false;
    const spaceshipAsset = this.gameService.positionAsset(
      this.spaceship,
      AssetAlignEnum.center,
      AssetAlignVerticalEnum.bottom
    );
    spaceshipAsset.y = this.boundary.height - this.spaceship.getAssetHeight();
    this.spaceship.move(spaceshipAsset);

    this.spaceships.map((spaceship: SpaceshipClass) => {
      spaceship.isDestroyed = false;
    });
  }

  /**
   * Get the Spaceship Y Position
   */
  public getSpaceshipYPosition(): number {
    return this.spaceship.y;
  }

  /**
   * If the active spaceship is hit
   */
  public isHit(alienBombAsset: IAsset): void {
    this.spaceship.isHit(alienBombAsset);
  }

  /**
   * Get the active reserve spaceships
   */
  private getActiveSpaceshipReserves(): SpaceshipClass[] {
    return this.spaceships.filter((spaceship: SpaceshipClass) => !spaceship.isDestroyed);
  }

  /**
   * Remove a destroyed reserve spaceship
   */
  private removeSpaceshipReserve(): void {
    const reserves = this.getActiveSpaceshipReserves();
    if (reserves.length) {
      reserves[reserves.length - 1].isDestroyed = true;
    }
  }

  /**
   * Animate the invasion
   */
  public animate(now = 0): void {
    this.getActiveSpaceshipReserves().map((spaceship: SpaceshipClass) => {
      spaceship.animate(now);
    });

    this.spaceship.animate(now);
  }
}
