import { ASSET_ANIMATION } from './spaceship-2-constants';
import { GameService } from '../../service/game.service';
import { SpaceshipClass } from '../spaceship/spaceship.class';
import { BoundarySetupModel } from '../../model/boundary-set.model';

/**
 * The Spaceship 2 Class
 */
export class Spaceship2Class extends SpaceshipClass {
  /**
   * Constructor
   * @param gameService The GameService
   * @param ctx The ctx
   * @param boundarySetup The BoundarySetupModel
   * @param isPrimary if the ship is the primary ship
   */
  constructor(
    gameService: GameService,
    ctx: CanvasRenderingContext2D,
    boundarySetup: BoundarySetupModel,
    isPrimary: boolean
  ) {
    super(gameService, ctx, boundarySetup, isPrimary);
  }

  /**
   * Spawn the spaceship
   */
  protected override spawn(): void {
    this.assetAnimations = ASSET_ANIMATION;
    this.engineAnimations = [[]];
    super.spawn();
  }
}
