import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';

import { GameService } from '../../../service/game.service';
import { AssetAlignVerticalEnum } from '../../../asset/enum/asset-align-vertical.enum';
import { AssetAlignEnum } from '../../../asset/enum/asset-align.enum';
import { AssetConfigurationModel } from '../../../model/asset-configuration.model';
import { BoundarySetupModel } from '../../../model/boundary-set.model';
import { AlienBoss1Class } from '../../alien-boss-1/alien-boss-1.class';
import { AlienBossClass } from '../alien-boss.class';
import { AlienBoss2Class } from '../../alien-boss-2/alien-boss-2.class';
import { DebugDataModel } from '../../../model/debug-data.model';
import { GameCookieService } from '../../../service/game-cookie.service';

/**
 * The Alien Component
 */
@Component({
  selector: 'bp-alien-boss',
  templateUrl: './alien-boss.component.html',
  styleUrls: ['./alien-boss.component.scss']
})
export class AlienBossComponent implements OnInit {
  /**
   * The canvas
   */
  @ViewChild('board', { static: true })
  canvas: ElementRef<HTMLCanvasElement>;

  /**
   * CTS
   */
  private ctx: CanvasRenderingContext2D;

  /**
   * request Id
   */
  private requestId: number;

  /**
   * request
   */
  private isAnimateOnce: boolean;

  /**
   * aliens
   */
  private alienBosses: AlienBossClass[];

  /**
   * The alien
   */
  private alienBoss: AlienBossClass;

  /**
   * Debug Assets
   */
  public assets: AssetConfigurationModel[];

  /**
   * The Constructor
   * @param gameService The GameService from DI
   * @param gameCookie The Game Cookie Service
   */
  constructor(private gameService: GameService, private gameCookieService: GameCookieService) {
    this.isAnimateOnce = true;
    this.assets = [
      {
        key: 'alienBoss',
        name: 'Boss'
      } as AssetConfigurationModel,
      {
        key: 'alienBoss2',
        name: 'Boss 2'
      } as AssetConfigurationModel
    ];
  }

  /**
   * On Init
   */
  ngOnInit() {
    const columns = 20;
    const rows = 20;
    this.ctx = this.gameService.initBoard(this.canvas, rows, columns, 25);
    this.addAlienBosses({
      x: 0,
      y: 0,
      height: rows,
      width: columns
    } as BoundarySetupModel);

    this.toggleAssets(this.gameCookieService.getSecondaryDebugData());

    this.animate();
  }

  /**
   * Add the aliens
   */
  private addAlienBosses(boundary: BoundarySetupModel): void {
    this.alienBosses = [];

    const boss = new AlienBoss1Class(this.gameService, this.ctx, boundary, 1);
    boss.move(boss.positionAsset(AssetAlignEnum.center, AssetAlignVerticalEnum.middle));
    boss.isAnimateOnce = true;
    this.alienBosses.push(boss);

    const boss2 = new AlienBoss2Class(this.gameService, this.ctx, boundary, 1);
    boss2.move(boss2.positionAsset(AssetAlignEnum.center, AssetAlignVerticalEnum.middle));
    boss2.isAnimateOnce = true;
    this.alienBosses.push(boss2);

    this.alienBoss = this.alienBosses[1];
    this.alienBoss.isAnimateOnce = true;
    this.alienBoss.isDestroyed = false;
  }

  /**
   * Animate the object once
   */
  animateAsset(): void {
    this.isAnimateOnce = !this.isAnimateOnce;
    this.alienBoss.isDestroyed = false;
    this.alienBoss.move(this.alienBoss.positionAsset(AssetAlignEnum.center, AssetAlignVerticalEnum.middle));
    cancelAnimationFrame(this.requestId);
    this.animate();
  }

  /**
   * Draw the board
   */
  private draw() {
    this.ctx.clearRect(0, 0, this.ctx.canvas.width, this.ctx.canvas.height);
    this.gameService.addOutlines(this.ctx, 1, 0.025);
  }

  /**
   * Animate
   */
  public animate(): void {
    this.draw();
    this.alienBoss.animate();
    if (!this.isAnimateOnce) {
      this.requestId = requestAnimationFrame(this.animate.bind(this) as FrameRequestCallback);
    }
  }

  /**
   * toggleAssets
   *
   * @param key The asset to display
   */
  toggleAssets(key: string): void {
    this.alienBoss.isDestroyed = true;
    this.draw();
    let index = 0;
    this.alienBosses.map((alienBoss: AlienBossClass) => {
      alienBoss.isDestroyed = true;
    });
    this.assets.map((asset: AssetConfigurationModel, $index: number) => {
      if (key === asset.key) {
        index = $index;
        this.gameCookieService.setDebugData({
          secondaryAssetName: key
        } as DebugDataModel);
      }
    });

    this.alienBoss = this.alienBosses[index];
    this.animateAsset();
  }
}
