import { ASSET_ANIMATION, COLORS } from './number-constants';
import { GameService } from '../service/game.service';
import { AssetClass } from '../asset/asset.class';
import { BoundarySetupModel } from '../model/boundary-set.model';

/**
 * The Number Class
 */
export class NumberClass extends AssetClass {
  /**
   * The numbers
   */
  private numbers: number[];

  /**
   * Constructor
   * @param gameService The GameService
   * @param ctx The ctx
   * @param boundarySetup The BoundarySetupModel
   * @param color The Color of the numbers
   */
  constructor(
    gameService: GameService,
    ctx: CanvasRenderingContext2D,
    boundarySetup: BoundarySetupModel,
    private color: number
  ) {
    super(gameService, ctx, boundarySetup, 0);
    this.numbers = [];
  }

  /**
   * Spawn the bomb
   */
  protected override spawn(): void {
    this.assetAnimation = ASSET_ANIMATION[0];
    this.engineAnimation = [];
    this.destructionAnimations = [];
    super.spawn();
  }

  /**
   * Draw evenything necessary
   */
  protected draw(): void {
    this.ctx.save();
    this.ctx.translate(this.x, this.y);
    this.ctx.scale(2, 2);
    this.drawNumber();
    this.ctx.restore();
  }

  /**
   * Draw the bomb
   */
  private drawNumber(): void {
    let offset = 0;
    this.ctx.fillStyle = COLORS[this.color];
    this.numbers.map((scoreNumber: number) => {
      this.assetAnimation = ASSET_ANIMATION[scoreNumber];

      this.assetAnimation.forEach((row, y) => {
        row.forEach((value, x) => {
          if (value > 0) {
            this.ctx.fillRect(x + offset, y, 1, 1);
          }
        });
      });

      offset += 10;
    });
  }

  /**
   * Pad the points
   *
   * @param points The points to pad
   */
  private padPoints(points: number): string {
    let stringPoints = points.toString();
    if (points < 10) {
      stringPoints = `000${points}`;
    } else if (points < 100) {
      stringPoints = `00${points}`;
    } else if (points < 1000) {
      stringPoints = `0${points}`;
    }

    return stringPoints;
  }

  /**
   * Set the Numbers
   * @param inputNumber the number to display
   */
  public setNumber(inputNumber: number, isPadNumber = true): void {
    const numbers = isPadNumber ? this.padPoints(inputNumber) : [inputNumber];
    this.numbers = [];
    for (const index of numbers) {
      this.numbers.push(Number(index));
    }
  }

  /**
   * Animate the bomb
   */
  public animate(): void {
    this.draw();
  }
}
