import { ASSET_ANIMATION } from './alien-boss-2-constants';
import { GameService } from '../../service/game.service';
import { BoundarySetupModel } from '../../model/boundary-set.model';
import { AlienBossClass } from '../alien-boss/alien-boss.class';

/**
 * The Alien Boss 2 Class
 */
export class AlienBoss2Class extends AlienBossClass {
  /**
   * Constructor
   * @param gameService The GameService
   * @param ctx The ctx
   * @param boundarySetup The BoundarySetupModel
   * @param guid The guid of the alien
   */
  constructor(
    gameService: GameService,
    ctx: CanvasRenderingContext2D,
    boundarySetup: BoundarySetupModel,
    guid: number
  ) {
    super(gameService, ctx, boundarySetup, guid, 120);
  }

  /**
   * Spawn the alien
   */
  protected override spawn(): void {
    this.assetAnimations = ASSET_ANIMATION;
    super.spawn();
  }
}
