import { ASSET_ANIMATION, ENGINE_ANIMATION } from './missile-1-constants';
import { GameService } from '../../service/game.service';
import { MissileClass } from '../missile/missile.class';
import { BoundarySetupModel } from '../../model/boundary-set.model';

/**
 * The Missile 1 Class
 */
export class Missile1Class extends MissileClass {
  /**
   * Constructor
   * @param gameService The GameService
   * @param ctx The ctx
   * @param boundarySetup The BoundarySetupModel
   * @param guid The guid of the missle
   */
  constructor(
    gameService: GameService,
    ctx: CanvasRenderingContext2D,
    boundarySetup: BoundarySetupModel,
    guid: number
  ) {
    super(gameService, ctx, boundarySetup, guid);
  }

  /**
   * Spawn the missile
   */
  protected override spawn(): void {
    this.assetAnimations = ASSET_ANIMATION;
    this.engineAnimations = ENGINE_ANIMATION;
    super.spawn();
  }
}
