import { Injectable } from '@angular/core';
import { CookieService } from 'ngx-cookie-service';
import { GameDataModel } from '../model/game-data.model';
import { DebugDataModel } from '../model/debug-data.model';

/**
 * The Cookie service
 */
@Injectable({
  providedIn: 'root'
})
export class GameCookieService {
  /**
   * The constructor
   * @param cookieService The cookieService from DI
   */
  constructor(private cookieService: CookieService) {}

  /**
   * Get the cookie
   */
  private getCookie(cookieName: string): string {
    return this.cookieService.get(cookieName);
  }

  /**
   * Get the High Score
   */
  public getGameData(): GameDataModel {
    const cookie = this.getCookie('bp-space-invaders');
    return cookie ? (JSON.parse(cookie) as GameDataModel) : ({ highScore: 0 } as GameDataModel);
  }

  /**
   * Set the cookie
   */
  public setGameData(data: GameDataModel): void {
    this.cookieService.set('bp-space-invaders', JSON.stringify(data));
  }

  /**
   * Get the Debug information
   */
  public getDebugData(): string {
    const cookie = this.getCookie('bp-space-invaders-debug');
    /* eslint-disable-next-line */
    return cookie ? (JSON.parse(cookie)['assetName'] as string) : '';
  }

  /**
   * Get the Secondary Debug information
   */
  public getSecondaryDebugData(): string {
    const cookie = this.getCookie('bp-space-invaders-debug');
    /* eslint-disable-next-line */
    return cookie ? (JSON.parse(cookie)['secondaryAssetName'] as string) : '';
  }

  /**
   * Set the debug cookie data
   *
   * @param assetName The asset name
   */
  public setDebugData(debugData: DebugDataModel): void {
    const cookie = this.getCookie('bp-space-invaders-debug');
    const existingCookie = (cookie ? JSON.parse(cookie) : {}) as DebugDataModel;
    existingCookie.assetName = debugData.assetName ? debugData.assetName : existingCookie.assetName;
    existingCookie.secondaryAssetName = debugData.secondaryAssetName
      ? debugData.secondaryAssetName
      : existingCookie.secondaryAssetName;
    this.cookieService.set('bp-space-invaders-debug', JSON.stringify(existingCookie));
  }
}
