import { GameService } from './game.service';
import { ObservableModel } from '../model/observable.model';
import { ObservableTypeEnum } from '../enum/observable-type.enum';
import { Missile2Class } from '../missile/missile-2/missile-2.class';
import { MissileClass } from '../missile/missile/missile.class';
import { BombClass } from '../bomb/bomb.class';
import { BoundarySetupModel } from '../model/boundary-set.model';

/**
 * The projectile service
 */
export class ProjectileService {
  /**
   * The spaceship missiles
   */
  private spaceshipMissiles: MissileClass[];
  /**
   * The game is paused
   */
  private isGamePaused: boolean;
  /**
   * The alien bombs
   */
  private alienBombs: BombClass[];

  /**
   * The constructor
   *
   * @param gameService The GameService
   * @param ctx The ctx
   * @param boundary The boundary setup model
   */
  constructor(
    private gameService: GameService,
    private ctx: CanvasRenderingContext2D,
    private boundary: BoundarySetupModel
  ) {
    this.spaceshipMissiles = [];
    this.alienBombs = [];
    this.isGamePaused = false;

    this.init();
  }

  /**
   * Init the service with the listener
   */
  private init(): void {
    this.gameService.getMasterObservable().subscribe((event: ObservableModel) => {
      if (event.type === ObservableTypeEnum.missileShot) {
        this.launchMissile(event);
      } else if (event.type === ObservableTypeEnum.bombDropped) {
        this.dropBomb(event);
      } else if (event.type === ObservableTypeEnum.gamePaused) {
        this.isGamePaused = event.booleanData;
      }
    });
  }

  /**
   * Find any unused missiles
   */
  private findUnusedMissile(): MissileClass {
    return this.spaceshipMissiles.filter((missile: MissileClass) => missile.isDestroyed)[0];
  }

  /**
   * Find any unused bombs
   */
  private findUnusedBomb(): BombClass {
    return this.alienBombs.filter((bomb: BombClass) => bomb.isDestroyed)[0];
  }

  /**
   * Return all Active Missiles
   */
  public getActiveMissiles(): MissileClass[] {
    return this.spaceshipMissiles.filter((missile: MissileClass) => !missile.isDestroyed);
  }

  /**
   * Return all Active Missiles
   */
  public getActiveBombs(): BombClass[] {
    return this.alienBombs.filter((bomb: BombClass) => !bomb.isDestroyed);
  }

  /**
   * Remove all the projectiles
   */
  public removeProjectiles(): void {
    this.getActiveMissiles().map((missile: MissileClass) => {
      missile.isDestroyed = true;
    });

    this.getActiveBombs().map((bomb: BombClass) => {
      bomb.isDestroyed = true;
    });
  }

  /**
   * Animate the missiles
   * @param now  the current time stamp
   */
  animate(now = 0): void {
    if (!this.isGamePaused) {
      this.getActiveMissiles().map((missile: MissileClass) => {
        missile.animate(now);
      });

      this.getActiveBombs().map((bomb: BombClass) => {
        bomb.animate(now);
      });
    }
  }

  /**
   * Launch a Missile
   * @param event The event
   */
  private launchMissile(event: ObservableModel): void {
    let missile = this.findUnusedMissile();
    if (!missile) {
      missile = new Missile2Class(this.gameService, this.ctx, this.boundary, this.spaceshipMissiles.length);
      this.spaceshipMissiles.push(missile);
    }
    missile.isDestroyed = false;

    missile.move(event.assetCoordinateData);
  }

  /**
   * Drop a bomb
   * @param event The event
   */
  private dropBomb(event: ObservableModel): void {
    let bomb = this.findUnusedBomb();
    if (!bomb) {
      bomb = new BombClass(this.gameService, this.ctx, this.boundary, this.alienBombs.length);
      this.alienBombs.push(bomb);
    }
    bomb.isDestroyed = false;

    bomb.move(event.assetCoordinateData);
  }
}
